@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.main {
  margin: 0%;
  padding: 0%;
  font-family: montserrat, arial, verdana;
  position: relative;
  overflow-x: hidden;
}
.body{
  background: white;
  padding: 2%;
  border-radius: 10px;
}
.form {
  width: 100%;
  height: 100%;
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  background-color: #ccf2ff;
}

/* PROGRESS BAR */
.progressbar {
  display: flex;
  width: 40%;
  max-width: 600px;
  height: 60px;
  justify-content: space-around;
  background-color: #0089ED;
  margin-bottom: 25px;
  margin-top: 25px;
  border-radius: 2px;
}

.progressbar div {
  width: 30.3%;
  margin: 0px 5px;
  height: 80%;
  justify-self: center;
  align-self: center;
  border-radius: 10px;
}

.form-container {
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.name {
  width: 100%;
}

.label,
.label2 {
  display: flex;
  flex-direction: column;
  align-items: start;
  width: 100%;
}

.label-name {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  width: 100%;
}

.radios label, .checkboxes label {
  padding: 15px;
}

#l2{
  margin-top: 1em;
}

.Ageny-Details-container,
.Candidate-Info-container,
.document-container {
  height: 100%;
  width: 100%;
  margin-right: 5em;
  display: flex;
  flex-direction: column;
  font-size: 15px;
  font-weight: bold;
  margin-bottom: 20px;
}


.Ageny-Details-container input,
.Candidate-Info-container input,
.document-container input {
  width: 100%;
  height: 2.5em;
  padding-left: 5px;
  font-size: 15px;
}


input {
  border: 2px solid #0089ED;
  border-radius: 5px;
}

input:focus {
  border: 3px solid #0089ED;

}
/* .checkboxes input{
  width: 30%;
} */
input[type=radio], input[type=checkbox] {
  margin: 3%;
  width: 5%;
  height: 14px;
}

.radios,.checkboxes {
  display: flex;
  align-self: normal;
  margin: 2%;
  font-size: 15px;
}

/* .checkboxes{
  justify-content: space-between;
} */

.form-container .pageChange {
  flex: 20% 1;
  width: 45%;
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
  margin-bottom: 30px;
}

.form-container .pageChange button {
  border-radius: 7px;
  width: 7.5em;
  height: 40px;
  background-color: #0089ED;
  font-weight: bold;
  color: white;
  border: 0 none;
  border-radius: 1px;
  cursor: pointer;
  padding: 10px 5px;
  margin: 5px;
}


@media (min-width:750px) {
  .name {
    display: flex;
    grid-gap: 1.5em;
    gap: 1.5em;
  }

  .flex {
    display: flex;
    grid-gap: 2.5em;
    gap: 2.5em;
  }
  
  .document-container{
    margin: 0em 20em 0 0;
  }
}

@media (max-width:750px) and (min-width:550px) {
  .name {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 0.5em;
    gap: 0.5em;
  }
  .document-container{
    margin: 1em 0em 0 0;
  }

  .Ageny-Details-container input,
  .Candidate-Info-container input,.document-container input  {
    width: 95%;
    height: 2em;
    padding-left: 5px;
    font-size: 15px;
  }

  .label-name {
    width: 49%;
  }

  .flex {
    display: flex;
    grid-gap: 0.5em;
    gap: 0.5em;
  }

  #ln {
    width: 215%;
  }
  /* .checkboxes input{
    width: 20%;
  } */

  input[type=radio], input[type=checkbox]  {
    margin: 3%;
    width: 5%;
    height: 14px;
  }
  
  /* .checkboxes{
    justify-content: space-between;
  } */
}

@media (max-width:550px) {

  .Ageny-Details-container input,
  .Candidate-Info-container input  {
    width: 90%;
    height: 2em;
    padding-left: 5px;
    font-size: 15px;
  }
  .document-container{
    width: 80%;
    margin: 1em -5em 0 0;
  }
  /* .checkboxes input{
    width: 20%;
  } */
   
  input[type=radio], input[type=checkbox]  {
    margin: 3%;
    width: 5%;
    height: 14px;
  }
  /* .checkboxes{
    justify-content: space-between;
  } */
  
}

.main_card_container {
    width: 90vw;
    height: 4vw;
    overflow: hidden;
    border-radius: 1vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    position: relative;
}

.card_heading {
    color: #000;
    padding: 1.5vw;
    font-size: 1vw;
    font-family: "Poppins", sans-serif;
    font-weight: bold;
}

.heading{
    font-size: 1.8vw;
    font-family: "Poppins", sans-serif;
    font-weight: bold;
    margin-left: 10%;
}
.card_button {
    height: 2em;
    width: 8em;
    background-color: #0089ED;
    border-radius: 5px;
    color: #000;
    font-weight: bold;
    padding: 4px;
    font-size: 1vw;
    font-family: "Poppins", sans-serif;
    font-weight: bold;
}

.card_button{
    display: block;
    height: 2em;
    width: 2em;
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
}
.card_button img {
    height: 2em;
    width: 2em;
}

.content{
    margin: 5px;
}

#btnHeading{
    margin-left: 5%;
    margin-right: 5%;
}
@media only screen and (max-width: 1030px) {
    .main_card_container {
        width: 80vw;
    }

    .heading_containers {
        margin-top: 5vh;
    }
}

@media only screen and (max-width: 750px) {
    .main_card_container {
        width: 80vw;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }

    .card_heading {
        font-size: 2vw;
    }
    .card_button img{
        height: 1.5em;
     }
    .heading{
        font-size: 2.3vw;
    }
}

@media only screen and (max-width: 430px) {
    .main_card_container {
        width: 80vw;
        justify-content: space-evenly;
    }

    .card_heading {
        font-size: 2.5vw;
    }

    .card_button img {
       height: 1.3em;
    }
    .heading{
        font-size: 2.8vw;
    }
}

